.navbar {
	display: flex;
	align-items: center;
	justify-content: space-between;
	height: 60px;
	width: 100%;
	margin: 0;
	/* background-color: gray; */
}

.title {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 200px;
	border-radius: 8px;
	margin: 0;
	padding: 10px;
	color: rgb(0, 0, 0);
	font-family: 'Sansita', sans-serif;
	color: white;
	font-size: 32px;
}

.movie-modal {
	height: 300px;
	width: 300px;
	background-color: black;
}

.thumbnail {
	max-width: 150px;
	max-height: 250px;
}

.movie-card {
	margin: 50px;
	width: 200px;
	height: 320px;
	border-radius: 10px;
	background-color: rgb(109, 109, 109);
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.result-title {
	font-size: 12;
	text-align: center;
	color: white;
	white-space: pre-wrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

.no-image {
	width: 150px;
	height: 200px;
	max-height: 350;
	color: white;
	background-color: gray;
	border: 1px solid white;
}

