.search-container {
	margin-left: auto;
	/* margin: 0 auto; */
	background: #fff;
	height: 50px;
	border-radius: 50px;
	padding: 10px 20px;
	display: flex;
	align-items: center;
	justify-content: flex-end;
	transition: 0.8s;
	box-shadow: 0 0 25px 0 rgba(0, 0, 0, 0.4);
}

.search-container:hover input {
	width: 350px;
}

.search-container input {
	background: transparent;
	border: none;
	outline: none;
	width: 0px;
	font-weight: 500;
	font-size: 16px;
	transition: 0.8s;
}

.search-container input:focus {
	width: 350px;
}

@media (max-width: 600px) {
	.search-container {
		width: 300px;
		transition: none;
	}

	.search-container input {
		width: 300px;
	}
}

.result-area {
	margin-top: 50px;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	/* max-width: 120%; */
	align-items: center;
	padding-top: 20px;
	border-radius: 25px;
	background-color: rgba(185, 184, 184, 0.2);
}

.results {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	max-width: 120%;
	margin-top: 30px;
	align-items: center;
	justify-content: center;
}

.result-header {
	display: flex;
	align-items: center;
	flex-direction: row;
	margin: 0 auto;
	justify-content: space-between;
	width: 90%;
}

@media (max-width: 600px) {
	.result-header {
		flex-direction: column;
		justify-content: center;
	}

	.genre-name {
		position: relative;
		top: 115px;
	}

	.result-area {
		padding: 0px;
	}
}

.genre-name {
	font-size: 24px;
	color: white;
	margin-bottom: 10px;
}

